@import "~Styles/colors.module";

.wishlist_container {
  padding: 30px 60px 0px 60px;
  overflow: auto;

  .page_header {
    font-family: PlayfairDisplay;
    font-size: 36px;
    font-weight: 900;
    color: $primary-text-color;
  }

  .wishlist_list_container {
    flex-flow: wrap;
    padding-top: 20px;
  }
}

.empty_screen_container {
  margin-top: 50px;
}

.rtl {
  text-align: right;
  .wishlist_list_container {
    flex-direction: row-reverse;
  }
}
