@import "~Styles/colors.module";
@import "~Styles/mixins.module";

.side_nav_container {
  flex: 1;
  align-self: stretch;
  overflow: auto;

  .nav_item:first-child {
    border-top: 0.5px solid rgba($color-light-blue-grey, 0.5);
  }

  .nav_item {
    border-bottom: 0.5px solid rgba($color-light-blue-grey, 0.5);
    padding: 8px 14px 8px 20px;
    @include on_click_button;

    .nav_image {
      width: 32px;
      height: 32px;
      opacity: 0.8;
      margin-right: 10px;
    }

    .nav_title {
      font-size: 12px;
      font-weight: bold;
      color: white;
    }

    .nav_description {
      font-size: 10px;
      font-weight: 300;
      color: white;
    }

    .nav_indicator {
      font-size: 16px;
      font-weight: 100;
      color: white;
      margin-left: auto;
    }
  }

  .is_selected {
    background: white;

    .nav_title {
      color: $primary-text-color;
    }

    .nav_description {
      color: $primary-text-color;
    }
  }
}

.rtl {
  direction: rtl;
  text-align: right;

  .nav_item {
    .nav_image {
      margin-left: 10px;
    }

    .nav_indicator {
      margin-left: 0px !important;
      margin-right: auto;
    }
  }

  .list_header {
    margin: 31px 20px 5px 0px !important;
  }
}
